@import "variables";
@import "mixins";

.by-pagination {
  display: inline-block;
  padding-left: 0;
}

.by-pagination li {
  display: inline;
}

.by-pagination li a {
  background-color: #fff;
  border: 1px solid #ccc;
  color: $by-pagination-color;
  float: left;
  font: 14px/16px "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0 2px;
  padding: 2px 4px;
  position: relative;
  text-decoration: none;
}

.by-pagination li a:hover,
.by-pagination li a:focus {
  background-color: #eee;
  border-color: #ccc;
  color: $by-pagination-color;
}

.by-pagination li span {
  float: left;
  margin: 0 2px;
  position: relative;
}

.by-pagination li.active a {
  background-color: $by-pagination-color;
  border-color: $by-pagination-color;
  color: #fff;
}

.by-pagination li a.disabled {
  background-color: #f3f3f3;
  border-color: #ccc;
  color: #ccc;
  cursor: not-allowed;
}

